@import url(https://fonts.googleapis.com/css2?family=Quicksand&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Acme&family=Catamaran&display=swap);

html{
  /* background-image: linear-gradient(to bottom right, rgb(81, 69, 248), rgb(111, 113, 228)); */
  background-image: linear-gradient(to bottom right, rgb(60, 212, 218), rgb(123, 248, 98));
  background-attachment: fixed;
  scroll-behavior: smooth;

}

body::-webkit-scrollbar {
  width: 10px
} 

body::-webkit-scrollbar-thumb {   
background-color: rgb(0, 0, 0);   
}

.App {
  display: grid;
  grid-template-areas: 'menu container';

}

.menu{
  grid-area: menu;
  height: 100vh;
}

.content{
  padding-left: 100px;
  grid-area: auto;
  height: 100vh;
  width: 100vw;
}

body {
  margin: 0px;
  padding: 0px;
}





.scheduler{display:grid;margin-top:20px;grid-gap:30px;grid-template-columns:repeat(8, 1fr)}.scheduler .calendar-task{grid-column:1 / 2}.scheduler .calendar-task .task-preview{margin-top:20px;margin-bottom:20px}.scheduler .painel{grid-column:2 / 8;align-self:center}@media (max-width: 800px){.scheduler{display:flex;flex-direction:column}}

.calendar .react-calendar{width:300px;height:340px;font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;box-shadow:0 0 0.6em #7e7d7d;border-radius:10px}.calendar .react-calendar__navigation button{font-size:20px}

.task-painel{width:85%;height:150px;margin:10px auto;border-radius:10px 0 0 10px;background-color:white;box-shadow:0 0 0.2em #2926e2;overflow:auto;scroll-behavior:smooth}.task-painel h2{background-color:#d5d5d8;margin:0}.task-painel p{text-align:justify;padding:15px}.task-painel .icons-sup{text-align:right;display:flex;justify-content:space-between;margin:5px}.task-painel .icons-sup-right{text-align:right;margin:5px}.task-painel .icons-inf{text-align:right}.task-painel .icon{font-size:20px;padding:3px;border-radius:20px}.task-painel .done{color:#07b107;border-radius:10px}.task-painel .undone{color:black;text-align:left;border-radius:10px}.task-painel .delete{color:#db1616}.task-painel .update{color:#006eff;margin-left:10px}.task-painel .done:hover{background-color:#07b107;color:white}.task-painel .undone:hover{background-color:black;color:white}.task-painel .delete:hover{background-color:#db1616;color:white}.task-painel .update:hover{background-color:#006eff;color:white}.task-painel::-webkit-scrollbar{width:10px}.task-painel::-webkit-scrollbar-thumb{background-color:#1f08a0}

.painel{width:100%;height:auto;background-color:white;font-family:'Quicksand', sans-serif;box-shadow:0 0 0.6em #7e7d7d;border-radius:10px;text-align:center;margin-bottom:40px}.painel h1{padding:10px;background-color:#d5d5d8}.painel .painel-main{height:100vh;margin:0px;display:grid;grid-template-columns:repeat(3, 1fr)}.painel .painel-main .painel-colunm{height:100vh;border-collapse:collapse;border:0.5px solid #2478e6;overflow:auto;scroll-behavior:smooth;border-radius:10px}.painel .painel-main .painel-colunm-tasks{height:100;display:grid;grid-template-columns:1fr}.painel .painel-main .painel-colunm p.type{margin:0;height:10%;font-family:'Indie Flower', cursive;background-color:#7aadf0;font-family:'Righteous', cursive;font-size:30px;display:flex;justify-content:center;align-items:center;margin:auto}.painel-colunm::-webkit-scrollbar{width:10px}.painel-colunm::-webkit-scrollbar-thumb{background-color:#1f08a0}

.task-preview{width:300px;background-color:white;font-family:"Segoe UI", Tahoma, Geneva, Verdana, sans-serif;box-shadow:0 0 0.6em #7e7d7d;border-radius:10px}.task-preview .create-task-title{background-color:#d5d5d8;display:flex;justify-content:center;border-bottom:2px solid #cccccc}.task-preview .create-task-title h2{font-family:"Righteous", cursive;text-transform:uppercase;font-size:20px;margin:4px 0}.task-preview .task-form{display:flex;flex-direction:column}.task-preview .task-form input{border:none;border-bottom:2.5px solid #000000;margin-bottom:15px;font-size:14px}.task-preview .task-form input:focus{box-shadow:0 0 0 0;outline:0}.task-preview .task-form #form-title{margin-top:15px;align-self:center;width:50%}.task-preview .task-form label{margin-left:10%;margin:0 0 5px 10%;font-size:15px;font-weight:600}.task-preview .task-form #description{font-size:15px;width:80%;align-self:center}.task-preview .task-form .form-date{margin-top:15px}.task-preview .task-form #date{width:50%;margin-left:10%}.task-preview .task-form .btn-send{width:120px;height:40px;border:none;border-radius:5px;box-shadow:0 0 0.6em #000;color:#ffffff;background:#006edc;align-self:center;margin:10px 0}.task-preview .task-form .btn-send:hover{background:#0300ce}.task-preview .task-form .btn-send:active{box-shadow:0 0 0.6em #006edc;background:#000000}



.sign-in-and-sign-up{width:80vw;display:flex;flex-direction:row;justify-content:space-between;margin:20px auto;padding-right:100px}@media (max-width: 800px){.sign-in-and-sign-up{width:90%;flex-direction:column}.sign-in-and-sign-up *{margin-top:20px}}

.sign-in{width:380px;height:auto;padding:20px;color:whitesmoke;background-color:rgba(0,0,0,0.685);border-radius:20px;font-family:'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif}.sign-in .form{display:flex;flex-direction:column;height:100%}.sign-in .title{margin:10px 0}.sign-in .buttons{text-align:center}.sign-in .input{height:30px;font-size:15px}.sign-in .celula{display:flex;flex-direction:column;margin-top:40px}.sign-in .label{font-size:20px}.sign-in .custom-button{margin-top:60px;min-width:165px;width:auto;height:50px;letter-spacing:0.5px;line-height:50px;font-size:15px;background-color:rgba(0,0,0,0.712);color:white;text-transform:uppercase;font-weight:bolder;border-color:whitesmoke;cursor:pointer;border-radius:10px;box-shadow:0 0 0.5em whitesmoke;border:none}.sign-in .custom-button:hover{background-color:whitesmoke;color:black;box-shadow:0 0 2em black;border:1px solid black}@media (max-width: 500px){.sign-in .buttons{width:90%;flex-direction:column}}@media (max-width: 800px){.sign-in{width:90%}}

.sign-up{width:380px;height:auto;padding:20px;color:whitesmoke;background-color:rgba(0,0,0,0.685);border-radius:20px;padding-bottom:0;font-family:'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif}.sign-up .form{display:flex;flex-direction:column}.sign-up .title{margin:10px 0}.sign-up .buttons{text-align:center;margin-bottom:20px}.sign-up .input{height:30px;font-size:15px}.sign-up .celula{display:flex;flex-direction:column;margin-top:20px}.sign-up .label{font-size:20px}.sign-up .custom-button{margin-top:60px;min-width:165px;width:auto;height:50px;letter-spacing:0.5px;line-height:50px;font-size:15px;background-color:rgba(0,0,0,0.712);color:whitesmoke;text-transform:uppercase;font-weight:bolder;border-color:whitesmoke;cursor:pointer;border-radius:10px;box-shadow:0 0 0.5em whitesmoke;border:none}.sign-up .custom-button:hover{background-color:whitesmoke;color:black;box-shadow:0 0 2em black;border:1px solid black}@media (max-width: 800px){.sign-up{width:90%}}

.menu{position:fixed;font-family:'Acme', sans-serif;font-family:'Catamaran', sans-serif}.menu .menu-item{font-size:20px !important}.menu .menu-item:hover{background-color:black}.menu .logotext{text-align:center}.menu .arrow-icon{font-size:30px}.menu .big-icon{color:whitesmoke;height:100%;align-items:center;justify-content:center}

